<template>
    <div class="donate">
        <p class="type-about" style="margin-top: 10px;">Donate</p>
        <h1 class="sixth-title">捐款方案</h1>
        <div class="donate-num"><span class="total">累積</span><span class="donate-money">100,000,000</span>
        </div>
    </div>
    <carousel :items-to-show="1" v-model="currentSlide" style="width: 100%;position: relative;">
        <slide v-for="slide in donateBanner" :key="slide" style="width: 100%;">
            <img :src="require(`../../assets/images/section5/${slide}`)" alt="" class="donate-img">
        </slide>
        <template #addons>
            <navigation />
            <!-- <pagination /> -->
        </template>
    </carousel>
    <div class="donate-card">
        <h2 class="my-2" style="color: #505050;size: 40px;font-weight: bold;">{{ donateName }}</h2>
        <div class="my-2">
            <span style="letter-spacing: 2px;">捐款新台幣</span>
            <span style="color: #EA7F1B;letter-spacing: 2px; margin-left: 5px;">NT${{ donatePrice }}</span>
        </div>
        <div class="donate-people-num " style="margin-bottom: 10px;">已有 {{ donatePeople }} 人贊助</div>
        <button class="go-to-donate" @click="goDonate(currentSlide)">前往捐款</button>
    </div>
    <a-modal v-model:open="open" footer :width="950">
        <div style="display: flex;">
            <div class="dialog-left">
                <img :src="require(`../../assets/images/section5/${danateImg}`)" alt="" class="dialog-img">
            </div>
            <div class="dialog-right">
                <div>
                    <div>
                        <span class="donate-name">{{ donateName }}</span>
                        <span>已有 9,957 人贊助</span>
                    </div>
                    <p class="donate-desc">
                        眾所皆知貓是外星人派來統治地球的。但牠們發現，只要一直喵喵叫，就能吃到很多好吃的，所以牠們決定要收人類為僕。除了飲食外，玩樂也是相當重要，要時刻注意主子的運動量，才能活得長長久久喔</p>
                </div>
                <div>
                    <p style="font-weight: bold;">捐款新台幣</p>
                    <div class="add-price">
                        <a-button style="width: 35%;" @click="changePrice('minus', currentDonateMoney)">-100</a-button>
                        <h2 class="donate-price">NT${{ currentDonateMoney }}</h2>
                        <a-button style="width: 35%;" @click="changePrice('plus', currentDonateMoney)">+100</a-button>
                    </div>
                    <div style="display: flex;margin: 15px 0px;">
                        <a-button class="submit-donate" @click="pay()">捐款</a-button>
                    </div>
                    <div style="display: flex; align-items: center;">
                        <input type="checkbox" id="horns" name="horns" v-model="checked"
                            style="margin-right: 5px;    margin-bottom: 2px;" />
                        <label for="horns"><span>勾選以同意捐款聲明</span><span class="statement"
                                @click="watchStatement()">捐款聲明</span></label>
                    </div>
                </div>
            </div>
        </div>
    </a-modal>
    <!-- 捐款聲明 -->
    <a-modal v-model:open="isState" footer style="padding: 30px;" :width="760">
        <h1 style="text-align: center;color: #505050;size: 60px;font-weight: bold;letter-spacing: 3px;">捐款聲明</h1>
        <p style="color: #45280D;font-size: 18px;font-weight: bold;">對單一貓星人捐贈額度規定</p>
        <p style="letter-spacing: 2px;">個人捐贈：10罐罐 / 天</p>
        <p style="letter-spacing: 2px;">對不同貓星人：一天最多十個罐罐</p>
        <p style="letter-spacing: 2px;">對流浪貓星人：一天最多五個罐罐</p>
        <p style="letter-spacing: 2px;">貓星人團體：一天最多三十個罐罐</p>
    </a-modal>
    <!-- 成功付款 -->
    <a-modal v-model:open="succeedPay" footer style="position: relative;" :width="950">
        <div style="height: 200px;"></div>
        <div class="succeed-screen"></div>
        <div class="succeed-content">
            <div>
                <img :src="require(`../../assets/images/section2/avator2.png`)" alt=""
                    style="width: 200px;position: relative;z-index: 999;left: 130px;">
                <img :src="require(`../../assets/images/section2/Vector2.png`)" alt="" style="width: 200px;"
                    class="vector-position">
                <img :src="require(`../../assets/images/icon/fish.png`)" alt="" style="width: 65px;" class="fish-position">
            </div>
            <div style="margin-top: 60px;">
                <h2 style="text-align: center;font-weight: bold;font-size: 30px;">已成功捐款</h2>
                <p style="text-align: center;">謝謝您的支持，您的支持是我們最大的動力</p>
                <div style="display: flex;margin: 15px 0px;">
                    <a-button class="back-page" @click="backPage()">回到首頁</a-button>
                </div>
            </div>

        </div>
    </a-modal>
</template>
<script>
import 'vue3-carousel/dist/carousel.css'
import { Carousel, Slide, Pagination, Navigation } from 'vue3-carousel'
export default {
    name: 'SectionFifth',
    components: {
        Carousel,
        Slide,
        Pagination,
        Navigation,
    },
    watch: {
        currentSlide: {
            handler: function (newVal) {
                if (newVal == 0) {
                    this.donateName = "喵星之友";
                    this.donatePrice = 6000;
                    this.donatePeople = "2,000";
                    this.danateImg = "cat1.png";
                } else if (newVal == 1) {
                    this.donateName = "喵星大使";
                    this.donatePrice = 6000;
                    this.donatePeople = "2,000";
                    this.danateImg = "cat2.png";
                } else if (newVal == 2) {
                    this.donateName = "喵星傳奇";
                    this.donatePrice = 600;
                    this.donatePeople = "999";
                    this.danateImg = "cat2.png";
                }
            }
        }
    },
    data() {
        return {
            donateBanner: ['donate1.png', 'donate2.png', 'donate3.png'],
            open: false,
            currentSlide: 0,
            donateName: "喵星之友",
            donatePrice: 6000,
            donatePeople: 2000,
            danateImg: 'cat1.png',
            currentDonateMoney: 6000,
            checked: false,
            isState: false,
            succeedPay: false

        }
    },
    methods: {
        goDonate(index) {
            this.open = true;
            this.currentDonateMoney = this.donatePrice;
        },
        changePrice(type, price) {
            if (type == 'minus') {
                this.currentDonateMoney = price - 100;
            } else {
                this.currentDonateMoney = price + 100;
            }
        },
        watchStatement() {
            this.isState = true;
        },
        pay() {
            this.open = false;
            this.succeedPay = true;
        },
        backPage() {
            this.succeedPay = false;
        }
    }
}
</script>
